@charset 'utf-8';

/*
 * reset
 */

html, body, div, p, input, label {
  padding: 0;
  margin: 0;
  line-height: 1.5;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  height: 100%;
  font-size: 62.5%;
  font-family: verdana, arial, helvetica, sans-serif;
}

body {
  height: 100%;
  background: #fff;
}

p {
  margin-bottom: 2rem;
  font-size: 1.6rem;
}

.root__container {
  width: 100vw;
  height: auto;
  min-height: 100vh;
  margin: 0 auto;
  position: relative;
}

.container__subjects {
  width: 96rem;
  height: auto;
  padding: 4rem;
}

.subject__input,
.subject__sum,
.subject__average,
.subject__result {
  margin-bottom: 4rem;
}

.subject__input p:last-child {
  margin-bottom: 0;
}

.subject__result {
  margin-bottom: 0;
}

.subject__sum p label,
.subject__average p label {
  font-weight: 600;
}

.subject__result p {
  padding-bottom: 4rem;
  margin-bottom: 0;
}

.subject__result p:last-child {
  padding-bottom: 0;
}

.btn {
  display: inline-flex;
  align-items: center;
  padding: .8rem 1.6rem;
  border-radius: .4rem;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
}

.btn-primary {
  background: #337ab7;
}

.btn-success {
  background: #5cb85c;
}

.btn-warning {
  background: #f0ad4e;
}

.result {
  padding: 1.2rem 2.4rem;
  border: 1px solid #c1e6ed;
  border-radius: .4rem;
  background: #daecf6;
  font-weight: 600;
  color: #2f6f8e;
}

.subject__result-declaration {
  display: none;
}